const baseList = [{
url: 'javascript:',
img: '',
title: '今日必撸-不可错过的羊毛'
}]

export default {
name: 'trade-list',
components: {
Flexbox,
FlexboxItem,
Grid,
GridItem,
Swiper,
SwiperItem
},
data() {
return {
tradeCategorys: [
[
{ label: '我要买', link: '/trade/buy' },
{ label: '我要卖', link: '/trade/sell' }
],
[
{ label: '券码招商', link: '/trade/business' },
{ label: '券码资讯', link: '/trade/information' }
]
],
swiperItemIndex: 1,
swiperItems: baseList
}
},
methods: {}
}



